<template>
    <div id="FAQ">
        <AutoTemplate :aboutAPlus_auto_Arr=FAQ_Arr />
    </div>
</template>
<script>
import AutoTemplate         from    './AboutAPlus_autoTemplate.vue'         // 
const components = { AutoTemplate }
export default {
    data() {
        return {
            FAQ_Arr: [
                {
                    title:         `Q`,
                    textContent:   `A
                                    
                                    `
                },
                {
                    title:         `Q`,
                    textContent:   `A
                                    `
                },
                {
                    title:         `Q`,
                    textContent:    `A
                                    `
                }
            ]
        }
    },
    components: components
}
</script>
<style lang="sass">
@import '../../sass/main.sass'

#FAQ
    +auto--contentBox($autoMargin,$title-color,$title-size,$badgeFont-color,$text-size)
    +bC($F)
</style>



