<!--  -->
<template>
    <div id="privacyPolicy">
        <p class="describe--text"> {{ describe.title }} </p>
        <AutoTemplate :aboutAPlus_auto_Arr = privacyPolicy_Arr />
        <!-- 3 -->
        <p class="principle3" v-for = "item in principle3_Arr"> {{ item.textContent }} </p>
        <!--  -->
        <div class="privacyPolicy--box disclaimer">
            <h3> {{ disclaimer.title }} </h3>
            <p> {{ disclaimer.textContent }} </p>
            <!--   -->
            <p class="disclaimer--Arr" v-for = "item in disclaimer_Arr">
                {{ item.textContent }}
            </p>
        </div>
        <!--  -->
        <div class="privacyPolicy--box terms">
            <h3> {{ terms.title }} </h3>
            <p class="terms--Arr" v-for = "item in terms.terms_Arr">
                {{ item.textContent }}
            </p>
            <p class="termsInfo--Arr" v-for = "item in terms.termsInfo_Arr">
                {{ item.textContent }}
            </p>
        </div>
    </div>
</template>
<script>
import AutoTemplate         from    './AboutAPlus_autoTemplate.vue'         // 
const components = { AutoTemplate }
export default {
    data() {
        return {
            describe: {
                title:              `
                                    
                                    `
            },
            privacyPolicy_Arr: [
                {
                    title:         ``,
                    textContent:   `
                                    
                                    
                                    
                                    
                                    
                                    `
                },
                {
                    title:         ``,
                    textContent:   `
                                    `
                },
                {
                    title:         ``,
                    textContent:   `
                                    
                                    `
                },
                {
                    title:         ``,
                    textContent:   ``
                },
                {
                    title:         ``,
                    textContent:   ``
                }
            ],
            principle3_Arr: [
                {
                    textContent:    `(1) `
                },
                {
                    textContent:    `(2) `
                },
                {
                    textContent:    `(3) `
                }
            ],
            disclaimer: {
                title:              ``,
                textContent:        ``
            },
            disclaimer_Arr: [
                {
                    textContent:    `(1) `
                },
                {
                    textContent:    `(2) 
                                    `
                },
                {
                    textContent:    `(3) `
                },
                {
                    textContent:    `(4) `
                }
            ],
            // 
            terms: {
                title:              ``,
                terms_Arr: [
                    {
                        textContent: `(1) `
                    },
                    {
                        textContent: `(2) `
                    },
                    {
                        textContent: `(3) `
                    },
                    {
                        textContent: `(4) `
                    }
                ],
                termsInfo_Arr: [
                    {
                        textContent: ``
                    },
                    {
                        textContent: ``
                    },
                    {
                        textContent: `
                                     `
                    }
                ]
            }
        }
    },
    components: components
}
</script>
<style lang="sass">
@import '../../sass/main.sass'

#privacyPolicy
    +auto--contentBox($autoMargin,$title-color,$title-size,$badgeFont-color,$text-size)
    +bC($F)
    .describe--text
        +REM(margin-bottom,$autoMargin)
    .privacyPolicy--box
        +REM(margin-top,$autoMargin)
</style>



