<template>
    <div id="index">
        <!--  -->
        <div v-if="this.$store.state.loadingState.judgeShow" id="loading">
            <img :src="loadingStyle.imgUrl" class="animated bounceInDown">
        </div>
        <!--  -->
        <div v-else>
            <router-view name="AppContent">      </router-view>
            <router-view name="AppBottomNav">    </router-view>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    data() {
        return {
            loadingStyle: {
                imgUrl: require('./assets/images/loading.png')
            }
        }
    }
    ,mounted: function () {
        this.addState()
        this.addLoadingAnimation()                         // Loading
    }
    ,methods: {
        // : Action 
        addState() {
            this.$store.dispatch({
                type: 'addState'
            })
        }
        // : 
        ,addLoadingAnimation() {
            // class
            setTimeout(() => {
                let loadingImg = document.getElementById("loading").firstChild          // loadingimg
                loadingImg.setAttribute("class","animated rubberBand")                  // class
            }, 1500)
        }
    }
}
</script>

<style lang="sass">
@import './sass/main'
@import './sass/vendors/MuseUi'

body
    @extend %clearScroll
#app
    position: relative
#loading img
    width: 100%
    object-fit: cover
</style>



