<!-- "" -->
<template>
    <div id="houseResource">
        <Auto_AppBar />
        <BuildingTabs />
        <BuildingList />
    </div>
</template>

<script>
import  BuildingList            from        '../components/Auto/BuildingList.vue'
import  BuildingTabs            from        '../components/HouseResource/Building_Tabs.vue'
import  Auto_AppBar             from        '../components/Auto/Auto_AppBar.vue'

const   components  = { BuildingList, BuildingTabs, Auto_AppBar }

export default {
    mounted: function() {
        this.setListObj_marginTop()
        this.setBottomBtnState(1)
    }
    ,methods: {
        // 
        setBottomBtnState(state) {
            let bottomBtn_Arr = document.getElementsByClassName("mu-buttom-item")
            for( let i = 0; i < bottomBtn_Arr.length; i++ ) {                                                           // 
                bottomBtn_Arr[i].setAttribute("class","mu-buttom-item router-link-active")
            }
            bottomBtn_Arr[state].setAttribute("class","mu-buttom-item router-link-active mu-bottom-item-active")        // ""
        }
        // :   
        ,setListObj_marginTop: function() {
            setTimeout(() => {
                const appBar_height     = document.getElementsByClassName("auto__AppBar")[0].offsetHeight       // AppBar
                const tabs_height       = document.getElementById("building_tabs").offsetHeight                 // tabs
                const buildingList_Obj  = document.getElementsByClassName("buildingList")[0]                    // 
                buildingList_Obj.style.marginTop = appBar_height + tabs_height + 18 + "px"                      //   margin-top( $autoMargin )
            }, 100)
        }
    }
    ,components: components
}
</script>

<style lang="sass" scoped>
@import '../sass/main'

#houseResource
    +REM(margin-bottom,80px)
</style>



