<!--  /  -  -->
<template>
    <div class="buildingDetails_around">
        <div id="buildingDetails--map" v-bind:data-point-longitude="building_centrePoint.longitude" v-bind:data-point-latitude="building_centrePoint.latitude" ></div>
        <!--  -->
        <div class="around__title">
            <div class="title--flexbox">
                <p> {{ building_centrePoint.name }} </p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['building_centrePoint']
    ,mounted: function() {
        this.setMap_height()
        this.asyncLoadMap()
    }
    ,methods: {
        setMap_height: function(){
            const mapObj                    = document.getElementById('buildingDetails--map')               // 
            const mapWidth                  = mapObj.offsetWidth                                            // 
            mapObj.style.height             = mapWidth + "px"                                               // 
            mapObj.style.backgroundColor    = "rgb(255,94,27)"                                              // 
        }
        // Promise
        ,asyncLoadMap: function(){
            // ,ajax,
            setTimeout(() => {
                // 
                const mapObj            = document.getElementById("buildingDetails--map")                                                                       // 
                const mapLongitude      = parseFloat(mapObj.getAttribute("data-point-longitude"))                                                               // (  )
                const mapLatitude       = parseFloat(mapObj.getAttribute("data-point-latitude"))                                                                // (  )

                // API
                const buildingMap           = new BMap.Map("buildingDetails--map")
                const buildingMap_Point     = new BMap.Point(mapLongitude,mapLatitude)
                const buildingMap_Icon      = new BMap.Icon("http://www.aplusoffice.cn/images/resourceMap/ico-pin-orange-24-px@2x.png", new BMap.Size(50,50))
                const buildingMap_marker    = new BMap.Marker(buildingMap_Point,{icon:buildingMap_Icon})                                                       // ()
                buildingMap.centerAndZoom(buildingMap_Point, 16)
                buildingMap.addOverlay(buildingMap_marker)                                                                                                     // 
                buildingMap.disableDragging()                                                                                                                  // 
                buildingMap_marker.setAnimation(BMAP_ANIMATION_BOUNCE)                                                                                         // 

                // 
                // const sContent =""
                // const infoWindow = new BMap.InfoWindow(sContent)                                                                                   // 
                // const content_point = new BMap.Point(120.384459,36.072709)
                // buildingMap.openInfoWindow(infoWindow,content_point)                                                                                       //
            }, 2000)
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../../sass/main'

.buildingDetails_around
    position: relative
    // 
    #buildingDetails--map
    //  
    .around__title
        position: absolute
        top: 28%
        left: 26%
        +REM(padding,$autoMargin/2)
        width: 50%
        +bC($F)
        .title--flexbox
            @extend %flexCenter
            width: 100%
            >p
                +REM(font-size,$text-size)
                color: $theme-color
</style>



