 <!-- Map  -->
<template>
    <div id="map_dropDownMenu">
        <mu-dropDown-menu :value="buildingType" @change="buildingTypeChange">
            <mu-menu-item value="1" title=""/>
            <mu-menu-item value="2" title=""/>
        </mu-dropDown-menu>

        <mu-dropDown-menu :value="dayPrice" @change="dayPriceChange">
            <mu-menu-item value="1" title=""/>
            <mu-menu-item value="2" title=""/>
            <mu-menu-item value="3" title="3"/>
            <mu-menu-item value="4" title="3~5"/>
            <mu-menu-item value="5" title="5~8"/>
            <mu-menu-item value="6" title="8~10"/>
            <mu-menu-item value="7" title="10"/>
        </mu-dropDown-menu>

        <mu-dropDown-menu :value="decoration" @change="decorationChange">
            <mu-menu-item value="1" title=""/>
            <mu-menu-item value="2" title=""/>
            <mu-menu-item value="3" title=""/>

            <mu-menu-item value="4" title=""/>
            <mu-menu-item value="5" title=""/>
            <mu-menu-item value="6" title=""/>
        </mu-dropDown-menu>
    </div>
</template>

<script>
export default {
    data () {
        return {
            buildingType: '1'
            ,dayPrice: '1'
            ,decoration: '1'
        }
    },
    methods: {
        buildingTypeChange (value) {
            this.buildingType = value
        }
        ,dayPriceChange (value) {
            this.dayPrice = value
        }
        ,decorationChange (value) {
            this.decoration = value
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../../sass/main'

#map_dropDownMenu
    position: fixed
    z-index: 2
    @extend %flexWrap
    width: 100%
    border-bottom: 1px solid rgba($badgeFont-color,.3)
    >.mu-dropDown-menu
        flex: 1
        +bC($F)
        border-right: 1px solid rgba($badgeFont-color,.3)
        &:last-child
            border-right: none
</style>



