<!--  (  )  -->
<template>
    <div class="co-working_router">
        <!--  -->
        <CssLoading v-if="getCoWorkingInfo.judgeShow" />
        <!--   -->
        <div v-else>
            <!---->
            <DBAppBar :AppBar_title = getCoWorkingInfo.buildingDetails.name />
            <Swiper :swiper_arr = getCoWorkingInfo.imgUrl_Arr />
            <div class="intro">
                <h2> {{ getCoWorkingInfo.buildingDetails.name }} </h2>
                <ul>
                    <li>
                        <span> : </span>
                        <p class="origin"> {{ getCoWorkingInfo.buildingDetails.city }} </p>
                        <p> {{ getCoWorkingInfo.buildingDetails.reg }} </p>
                        <p class="end"> {{ getCoWorkingInfo.buildingDetails.road }} </p>
                    </li>
                    <li>
                        <span> : </span>
                        <p> <b> {{ getCoWorkingInfo.buildingDetails.amountA }}  </b> </p>
                    </li>
                    <li>
                        <span> : </span>
                        <p> <b> {{ getCoWorkingInfo.buildingDetails.amountB }}  </b> </p>
                    </li>
                    <li>
                        <span> : </span>
                        <p> {{ getCoWorkingInfo.buildingDetails.propertyCompany }} </p>
                    </li>
                    <li>
                        <span> : </span>
                        <p> {{ getCoWorkingInfo.buildingDetails.parkingNum }} </p>
                    </li>
                </ul>
            </div>
            <!-- '' -->
            <div class="contentBox">
                <Title :title_info = titleInfo />
                <p> {{ getCoWorkingInfo.buildingDetails.description }} </p>
            </div>
            <!--  -->
            <div class="contentBox buildingDetails--list">
                <Title :title_info = typeASearch />
                <ul>
                    <li v-for = "item in getCoWorkingInfo.typeASearch">
                        <!--  -->
                        <div>
                            <img :src="item.imgUrl" class="listImg" >
                        </div>
                        <!--  -->
                        <p> {{ item.area }}m </p>
                        <!--   -->
                        <p> <b> {{ item.priceMonth }} </b> <spsn>/</spsn></p>
                        <!--    -->
                        <p class="priceDay"> <b> {{ item.priceDay }} </b> <spsn>/m/</spsn></p>
                        <!--  -->
                        <p> {{ item.seatCount }} </p>
                    </li>
                </ul>
            </div>
            <!--  -->
            <div class="contentBox buildingDetails--list">
                <Title :title_info = typeBSearch />
                <ul>
                    <li v-for = "item in getCoWorkingInfo.typeBSearch">
                        <!--  -->
                        <div>
                            <img :src="item.imgUrl" class="listImg" >
                        </div>
                        <!--  -->
                        <p> {{ item.area }}m </p>
                        <!--   -->
                        <p>
                            <b> {{ item.priceMonth }} </b>
                            <span>/</span>
                        </p>
                        <!--    -->
                        <p class="priceDay">
                            <b> {{ item.priceDay }} </b>
                            <span>/m/</span>
                        </p>
                        <!--  -->
                        <p>  {{ item.seatCount }}  </p>
                    </li>
                </ul>
            </div>
            <!--  -->
            <div class="contentBox">
                <Title :title_info = reserve />
                <mu-icon slot="left" value="person"/> <mu-text-field label="" labelFloat/><br/>
                <mu-icon slot="left" value="phone"/> <mu-text-field label="" labelFloat/><br/>
                <mu-icon slot="left" value="vpn key"/>
                <mu-raised-button class="demo-raised-button" label="" icon="send" primary/>
            </div>
            <!--  -  -->
            <div class="contentBox">
                <Title :title_info = affiliation />
                <Intro :buildingIntro_Arr=getCoWorkingInfo.buildingRelationList />
            </div>
            <!--  -->
            <div class="contentBox">
                <Title :title_info = around />
                <Map :building_centrePoint = getCoWorkingInfo.buildingDetails />
            </div>
        </div>
    </div>
</template>
<script>
import  { mapActions, mapGetters }  from    'vuex'
import  DBAppBar                    from    './BuildingDetails_AppBar.vue'      // 
import  Intro                       from    './BuildingDetails_Intro.vue'       // Intro 
import  CssLoading                  from    '../Auto/CssLoading_1.vue'          // 
import  Title                       from    '../Auto/Title.vue'                 // 
import  Swiper                      from    '../Discover/Swiper.vue'            // 
import  DetailsList                 from    './BuildingDetails_List.vue'        //   +  
import  Map                         from    './BuildingDetails_Map.vue'         //   (  )

const   components = { DBAppBar, Intro, CssLoading, Title, Swiper, DetailsList, Map }

export default {
    data() {
        return {
            titleInfo: {
                title: ''
                ,backgroundColor: '#FFF'
            }
            ,typeASearch: {
                title: ''
                ,backgroundColor: '#FFF'
            }
            ,typeBSearch: {
                title: ''
                ,backgroundColor: '#FFF'
            }
            ,reserve: {
                title: ''
                ,backgroundColor: '#FFF'
            }
            ,affiliation: {
                title: ''
                ,backgroundColor: '#FFF'
            }
            ,around: {
                title: ''
                ,backgroundColor: '#FFF'
            }
        }
    }
    ,mounted: function() {
        this.setCoWorkingInfo()                     // store 
        this.heightRevise()                         // List 
    }
    ,methods:{
        // : Action  (  )
        setCoWorkingInfo() {
            this.$store.dispatch({
                type: 'setCoWorkingInfo',
                codeId: this.$route.params.id                                       // ID
            })
        }
        // : List . 1:1
        ,heightRevise: function(){
            // ,ajax,
            setTimeout(() => {
                let img_Arr     = document.getElementsByClassName('listImg')
                let img_width   = img_Arr[0].offsetWidth                            // 
                for (let i = 0; i < img_Arr.length; i++){
                    img_Arr[i].style.height = img_width + 'px'                      // 
                }
            }, 4500)
        }
    }
    ,computed: mapGetters({
        getCoWorkingInfo: 'getCoWorkingInfo'
    })
    ,components: components
}
</script>
<style lang="sass" scoped>
@import '../../sass/main'

.co-working_router
    //   -   ( Sass )
    +auto--BuildingDetails
</style>



