<!-- "" - AppBar -->
<template>
    <mu-appbar id="discoverAppBar" >
        <mu-icon-button icon='list' slot="left" @click="toggle(true)" />
        <!--  -->
        <div class="discoverAppBar__search" @click="toSearch()">
            <mu-icon-button icon='search' />
        </div>
        <mu-icon-button icon='chat' slot="right" />
        <!--  -->
        <mu-drawer :open="open" :docked="docked" class="left--box" @close="toggle()">
            <mu-list @itemClick="docked ? '' : toggle()">
                <mu-list-item title="" @click="toAboutInfo( 'newFeatures' )" />                             <!-- NewFeatures -->
                <mu-list-item title=""   @click="toAboutInfo( 'FAQ' )" />                                     <!-- FAQ -->
                <mu-list-item title=""   @click="toAboutInfo( 'aboutUs' )" />                                 <!-- AboutUs -->
                <mu-list-item title=""   @click="toAboutInfo( 'cooperation' )" />                             <!-- Cooperation -->
                <mu-list-item title=""   @click="toAboutInfo( 'privacyPolicy' )" />                           <!-- PrivacyPolicy -->
                <mu-list-item title=""   @click="toAboutInfo( 'copyrightInfo' )" />                           <!-- CopyrightInfo -->
            </mu-list>
        </mu-drawer>
    </mu-appbar>
</template>
<script>
export default {
    data () {
        return {
            open: false,
            docked: true
        }
    },
    methods: {
        toggle (flag) {
            this.open   = !this.open
            this.docked = !flag
        }
        ,toSearch: function() {
            location.href = '#/search/'
        }
        ,toAboutInfo: function(pageName) {
            location.href = '#/aboutAPlus/' + pageName
        }
    }
}
</script>
<style lang="sass">
@import '../../sass/main'

.appbar-search-field
    color: #FFF
    margin-bottom: 0
    &.focus-state
        color: #FFF
    .mu-text-field-hint
        color: fade(#FFF,54%)
    .mu-text-field-input
        color: #FFF
    .mu-text-field-focus-line
        background-color: #FFF
// '' - AppBar
#discoverAppBar
    position: fixed
    +bC(rgba($F,.1))
    opacity: 1
    .mu-icon                                                                        // 
        color: $F !important
    .mu-appbar-title
        width: 72% !important
    // class( @click )
    .discoverAppBar__search
        @extend %flexCenter                                                         // 
    .left
        left: 0
        width: 14%
    .right
        right: 0
        width: 14%
        .mu-text-field                                                              // (  )
            position: absolute
            left: 15%
            padding-left: 38%!important
            margin-bottom: 0 !important
            >.mu-text-field-icon                                                    // 
                left: 44.5% !important
                top: 25% !important
            >div>div
                .mu-text-field-line,.mu-text-field-focus-line
                    left: 0 !important
        .mu-icon-button                                                             // (  )
            position: absolute
            right: 0
        .mu-text-field.has-icon
            padding-left: 34.5% !important                                          // 
        .focus-state
            >i.mu-icon
                display: none !important
</style>



