<!-- "" -->
<template>
    <div id="map">
        <!--  -->
        <div id="map__topNav">
            <Auto_AppBar class="Auto_AppBar"/>
            <DropDownMenu class="DropDownMenu" />
        </div>
        <!--<div id="map"></div>-->
    </div>
</template>

<script>
import  { mapActions, mapGetters }  from    'vuex'
import  Auto_AppBar                 from    '../components/Auto/Auto_AppBar.vue'    //  AppBar
import  DropDownMenu                from    '../components/Map/DropDownMenu.vue'    // ''
const   components  = { Auto_AppBar, DropDownMenu }

export default {
    mounted: function () {
        this.setBottomBtnState(2)
    },
    methods: {
        // : 
        setBottomBtnState(state) {
            let bottomBtn_Arr = document.getElementsByClassName("mu-buttom-item")
            for( let i = 0; i < bottomBtn_Arr.length; i++ ) {                                                           // 
                bottomBtn_Arr[i].setAttribute("class","mu-buttom-item router-link-active")
            }
            bottomBtn_Arr[state].setAttribute("class","mu-buttom-item router-link-active mu-bottom-item-active")        // ""
        }
    }
    ,components: components
}
</script>

<style lang="sass" scoped>
@import '../sass/main'

#map
    #map__topNav
        .Auto_AppBar,.DropDownMenu                              // appBar   (  )
            @extend %REL
</style>



