<template>
    <div id="Service_Furniture">
        <Service_dropDownMenu />
        <!--  -  -->
        <Furniture_scrollList />
    </div>
</template>

<script>
import      Service_dropDownMenu        from    '../Furniture/Furniture_dropDownMenu.vue'
import      Furniture_scrollList       from    '../Furniture_scrollList.vue'
const components = { Service_dropDownMenu,Furniture_scrollList }

export default {
    mounted: function () {
        this.setScrollHeight()
    },
    methods: {
        // :  Scroll,
        setScrollHeight: function() {
            setTimeout(() => {
                // appBar
                const appBar = document.getElementsByClassName('mu-paper-1')[0]                                      // 
                const appBar_height = appBar.offsetHeight                                                           // 
                const dropDownMenu = document.getElementsByClassName('Service_dropDownMenu')[0]
                dropDownMenu.style.top = appBar_height + "px"                                                       //  top(  )
                const dropDownMenu_height = dropDownMenu.offsetHeight                                               //  
                const windows_height = document.body.offsetHeight                                                   // 
                const service_contentHeight = windows_height - ( appBar_height + dropDownMenu_height + 10 ) + "px"  // (  )
                const service_content = document.getElementsByClassName("demo-infinite-container")[0]
                service_content.style.height = service_contentHeight                                                // 
                service_content.style.marginTop = dropDownMenu_height + "px"
            }, 200)
        }
    },
    components: components
}
</script>

<style lang="sass" scoped>
@import '../../../sass/main'

#Service_Furniture
</style>



