<!--  AppBar (  /  -  ) -->
<template>
    <div class="buildingDetails--AppBar">
        <mu-appbar >
            <mu-icon-button icon='arrow_back' slot="left" @click="returnIndex()"/>
            <h2> {{ AppBar_title }} </h2>
            <mu-icon-button icon='sms' slot="right"/>
        </mu-appbar>
    </div>
</template>
<script>
export default {
    props: ['AppBar_title']
    ,methods: {
        // : 
        returnIndex: () => {
            //  
            location.href = '#'
        }
    }
}
</script>

<style lang="sass">
@import '../../sass/main'

.buildingDetails--AppBar
    /* MuseUI - App Bar */
    +auto--AppBar
</style>



