import Vue       from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import app                          from '../App.vue'                                               // 
import bottomNav                    from '../views/BottomNav.vue'                                   // 
import discover                     from '../views/Discover.vue'                                    // "" 
import houseResource                from '../views/HouseResource.vue'                               // "" 
import map                          from '../views/Map.vue'                                         // "" 
import service                      from '../views/Service.vue'                                     // "" 
import search                       from '../views/Search.vue'                                      // "" 
// 
import aboutAPlus_router            from '../components/AboutAPlus/AboutAPlus_router.vue'           // '' - 
// 
import service_router               from '../components/Service/Service_router.vue'                 // ""  - 
import furniture_router             from '../components/Service/Furniture/Furniture_router.vue'     // ""  - ''
import research_router              from '../components/Service/Research/Research_router.vue'       // ""  - ''
//  (  /  )
import OfficeBuilding_router        from '../components/BuildingDetails/OfficeBuilding_router.vue'  // " (  )" - 
import CoWorking_router             from '../components/BuildingDetails/CoWorking_router.vue'       // " (  )" - 

const router = new VueRouter({
    routes: [
        {
            path: 'app',
            components: {
                default: app
            }
        },
        {
            path: '/',
            name:'index',
            components: { default: app },
            children: [
                {
                    path: '/',
                    components: {
                        AppContent      : discover,
                        AppBottomNav    : bottomNav
                    }
                },
                {
                    path: '/discover',
                    components: {
                        AppContent      : discover,
                        AppBottomNav    : bottomNav
                    }
                },
                {
                    path: '/houseResource',
                    components: {
                        AppContent      : houseResource,
                        AppBottomNav    : bottomNav
                    }
                },
                {
                    path: '/map',
                    components: {
                        AppContent      : map,
                        AppBottomNav    : bottomNav
                    }
                },
                {
                    path: '/service',
                    components: {
                        AppContent      : service,
                        AppBottomNav    : bottomNav
                    }
                },
                {
                    path: '/aboutAPlus/:id',                                                     // ''
                    components: {
                        AppContent      : aboutAPlus_router
                    }
                },
                {                                                                               //  ,  2
                    path: '/service/:id',
                    components: {
                        AppContent      : service_router
                    }
                },
                {                                                                               //  - '' 
                    path: '/service/furniture/:id',
                    components: {
                        AppContent      : furniture_router
                    }
                },
                {                                                                               //  - '' 
                    path: '/service/research/:id',
                    components: {
                        AppContent      : research_router
                    }
                },
                {                                                                               // " (  )" - 
                    path: '/office-Building/:id',
                    components: {
                        AppContent      : OfficeBuilding_router
                    }
                },
                {                                                                               // " (  )" - 
                    path: '/co-working/:id',
                    components: {
                        AppContent      : CoWorking_router
                    }
                }
                ,{                                                                               // 
                    path: '/search',
                    components: {
                        AppContent      : search
                    }
                }
            ]
        }
    ]
})

export default {
    router
}



