<!-- '' -  -->
<template>
    <div id="Service_Training">
        <div class="training--info bC--while">                                                              <!--  -->
            <Title :title_info = coreCourses />
            <p> {{ coreCourses.textContent }} </p>
        </div>
        <div class="training--info bC--lucency">                                                            <!--  -->
            <img :src="coursesImg.imgUrl" />
        </div>
        <div class="training--info bC--while">                                                              <!--  -->
            <Title :title_info = openCourses />
            <div class="openCourses--box" v-for="item in openCourses.coursesArr">
                <h3> {{ item.title }} </h3>
                <p> {{ item.textContent }} </p>
            </div>
        </div>
        <div class="training--info bC--while">                                                              <!--  -->
            <Title :title_info = overseasExchange />
            <img :src="overseasExchange.imgUrl" />
            <div class="overseasExchange--box" v-for="item in overseasExchange.exchangeArr">
                <h3> {{ item.title }} </h3>
                <p> {{ item.textContent }} </p>
            </div>
        </div>
        <div class="training--info bC--while">                                                              <!--  -->
            <Title :title_info = teacherIntroduction />
            <div class="teacherIntroduction--box" v-for="item in teacherIntroduction.teacherArr">
                <img :src="item.imgUrl" />
                <div class="teacherInfo" v-for="item in item.teacherInfoArr">
                    <h3> {{ item.title }} </h3>
                    <p> {{ item.textContent }} </p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import      Title        from    '../../Auto/Title.vue'
const components = { Title }

export default {
    data() {
        return {
            coreCourses: {
                title: '',
                backgroundColor: '#FFF',
                textContent:    ''
                            +   ''
                            +   ''
            },
            coursesImg: {
                imgUrl: require('../../../assets/images/coursesImg.png')
            },
            openCourses: {
                title: '',
                backgroundColor: '#FFF',
                coursesArr: [
                    {
                        title: '/13',
                        textContent:    '300900/'
                                    +   'ABB'
                                    +   'DHLUT'
                                    +   ''
                                    +   'SAP'
                    },
                    {
                        title: '',
                        textContent:    '1201500'
                                    +   ''
                    },
                    {
                        title: '',
                        textContent:    '50ABB'
                    },
                    {
                        title: '',
                        textContent:    '200'
                    },
                    {
                        title: '',
                        textContent:    ''
                    },
                    {
                        title: '/51E',
                        textContent:    '50ABB'
                    },
                    {
                        title: '',
                        textContent:    ''
                                    +   'TCL'
                                    +   'SGSLG'
                                    +   ''
                                    +   ''
                    }
                ]
            },
            overseasExchange: {
                title: '',
                backgroundColor: '#FFF',
                imgUrl: require('../../../assets/images/picOverseas@2x.png'),
                exchangeArr: [
                    {
                        title: '2013',
                        textContent:    '50'
                    },
                    {
                        title: '2014',
                        textContent:    '40'
                                    +   ''
                    }
                ]
            },
            teacherIntroduction: {
                title: '',
                backgroundColor: '#FFF',
                //    'teacherArr' 
                teacherArr: [
                    {
                        imgUrl: require('../../../assets/images/pic_profile.png'),
                        // ,  'teacherInfoArr'   
                        teacherInfoArr: [
                            {
                                title: '',
                                textContent:    '   JRC'
                                            +   ''
                                            +   'HR'
                                            +   ''
                                            +   '/2001/'
                                            +   '2005'
                                            +   '20'
                                            +   ''
                                            +   '2001'
                                            +   ''
                                            +
                                                '2004'
                                            +   '2009'
                                            +   '20122014'
                                            +   '2016'
                            },
                            {
                                title: '',
                                textContent:    'EMBA'
                                            +   'ISO9000'
                                            +   'TTTLIFO'
                                            +   'APEC'
                                            +   '(HBDI)'
                                            +   'LMI  '
                                            +   ' '
                            },
                            {
                                title: '',
                                textContent:    ''
                                            +   ''
                                            +   ''
                            }
                        ]
                    }
                ]
            }
        }
    },
    components: components
}
</script>

<style lang="sass" scoped>
@import '../../../sass/main'

#Service_Training
    +REM(margin-top,$autoMargin)
    .training--info                                                                                         // ''
        +auto--contentBox($autoMargin,$title-color-light,$title-size,$badgeFont-color,$text-size)
        +REM(margin-top,$autoMargin)
        img
            @extend %imgCover
        &:first-child
            margin: 0
        &:last-child
            +REM(margin-bottom,$autoMargin)
    .bC--while                                                                                              //  - 
        +bC($F)
        box-shadow: 0 .5px 1px rgba(0,0,0,0.12)
    .bC--lucency                                                                                            //  - 
        +bC(rgba($F,.1))
    .openCourses--box, .overseasExchange--box, .teacherInfo                                                 //  /  /   box
        +REM(margin-top,$autoMargin)
</style>



