export const addState = (state,res) => {
    let appTopImgUrl_Arr    = []
    let hotBuildingImg_Arr  = []

    //  
    for (let i = 0; i < res.appTopImg.length; i++){                                                                 //   - 
        function AppTopImg(imgUrl) {
            this.imgUrl = "http://images.aplusoffice.cn/" + imgUrl
        }
        const appTopImg_Obj = new AppTopImg( res.appTopImg[i] )
        appTopImgUrl_Arr.push(appTopImg_Obj)                                                                        //  store
    }
    state.bannerImg_Arr = appTopImgUrl_Arr
    //  
    for (var i = 0; i < res.building.length; i++) {
        function BuildingImg(imgUrl) {
            this.imgUrl = "http://images.aplusoffice.cn/" + imgUrl
        }
        const building_Obj = new BuildingImg( res.building[i].appListUrl )
        res.building[i].appListUrl = building_Obj                                                                       //  store
    }

    state.hotBuildingList_Arr.push(res.building)                                                                    //  
    // 
    for (var i = 0; i < res.building.length; i++) {
        if(res.building[i].type=='A'){
            state.hotBuildingList_Arr[0][i]['badge']        = ''
            state.hotBuildingList_Arr[0][i]['judgeShow']    = true
        }else{
            state.hotBuildingList_Arr[0][i]['badge']        = ''
            state.hotBuildingList_Arr[0][i]['judgeShow']    = false
        }
    }
    setTimeout(function() {
        state.loadingState['judgeShow'] = false            // ,false
    },6000)
}

// modules 
export const addFurnitureInfo = (state,res) => {
    // state( ; )
    state.furnitureInfo = {
        judgeShow: true
    }
    let furnitureImgUrl_Arr = []                                                                                    // ()
    let furnitureSizeList_Arr = []                                                                                  // ()
    for(let i=0; i<res.furnitureSList.length;i++) {                                                                 //   - 
        function FurnitureInfoImg(imgUrl) {
            this.imgUrl = res.picUrl + imgUrl
        }
        const furnitureImg_Obj = new FurnitureInfoImg( res.furnitureSList[i].supply )
        furnitureImgUrl_Arr.push(furnitureImg_Obj)                                                                  //  store
    }
    state.furnitureInfo['imgUrl'] = furnitureImgUrl_Arr                                                             //  state.furnitureInfo 
    //  - state.furnitureInfo ( code /  /  /  /  /  )
    for ( let i in res.furniture ) {
        if ( i.toString() === "appPic") {
            let appPicUrl = res.picUrl + res.furniture.appPic                                                       // Url: url + 
            state.furnitureInfo['appPicUrl'] = appPicUrl
        } else {
            state.furnitureInfo[i] = res.furniture[i]                                                               // statefurnitureInfo(  )
        }
    }
    //  - ''  (  +  )
    function FurnitureSizeObj(name,price){                                                                          //  -  => 
        this.name   = name
        this.price  = price
    }
    for ( let a = 0; a < res.p1List.length; a++ ) {
        let furnitureSize_Obj = new FurnitureSizeObj( res.p1List[a], res.p2List[a] )                                // 
        furnitureSizeList_Arr.push(furnitureSize_Obj)                                                               //  furnitureSizeList_Arr
    }
    state.furnitureInfo['furnitureSizeList'] = furnitureSizeList_Arr                                                // list state.furnitureInfo 

    setTimeout(function() {
        state.furnitureInfo['judgeShow'] = false            // ,false
        // console.log('')
    },4000)
}

// 
export const addResearchInfo =( state,res ) => {
    // state( ; )
    state.researchInfo = {
        judgeShow: true
    }
    // state.researchInfo = res
    state.researchInfo.appPic = res.picUrl + res.investigate.appPic                                                         // 
    state.researchInfo['name'] = res.investigate.name
    state.researchInfo['address'] = res.investigate.city + res.investigate.region + res.investigate.road + ''             // 
    state.researchInfo['description'] = res.investigate.description                                                         // 
    // let IPList = []
    // IPList = res.IPList
    state.researchInfo['IPList'] = res.IPList
    setTimeout(function() {
        state.researchInfo['judgeShow'] = false                                                                             // ,false
    },4000)
}

//  - 
export const addCoWorkingInfo =( state,res ) => {
    // state( ;  1., 2.buildingRelationList_judgeShow )
    state.coWorking = {
        judgeShow: true,
        buildingRelationList_judgeShow: true
    }
    // res   state ( judgeShow )
    state.coWorking.buildingDetails = res.buildingDetails                                                                   // (  +  )
    state.coWorking.buildingPicList = res.buildingPicList                                                                   // (  )
    state.coWorking.typeASearch     = res.typeASearch                                                                       // List
    state.coWorking.typeBSearch     = res.typeBSearch                                                                       // List
    let coWorkingImgUrl_Arr = []                                                                                            // ()
    for( let i=0; i<res.buildingPicList.length; i++ ) {                                                                     //   - 
        function CoWorkingImgUrl(imgUrl) {
            this.imgUrl = res.picUrl + imgUrl
        }
        const coWorkingImgUrl_Obj = new CoWorkingImgUrl( res.buildingPicList[i].realAppUrl )
        coWorkingImgUrl_Arr.push( coWorkingImgUrl_Obj )                                                                     //  store
    }
    state.coWorking['imgUrl_Arr'] = coWorkingImgUrl_Arr                                                                     //  state.furnitureInfo 
    // '' (  )
    for( let i=0; i<res.typeASearch.length; i++ ) {
        //    ( : ; :  )
        if ( res.typeASearch[i].appPic == "" ) {
            // console.log('')                                                                                            // 
            res.typeASearch[i].imgUrl = "http://images.aplusoffice.cn/images/resource2/default_list.jpg"                     //  
        } else {
            res.typeASearch[i].imgUrl = res.picUrl + res.typeASearch[i].appPic                                              // '' 
        }
    }
    // '' (  )
    for( let i=0; i<res.typeBSearch.length; i++ ) {
        //    ( : ; :  )
        if ( res.typeBSearch[i].appPic == "" ) {
            res.typeBSearch[i].imgUrl = "http://images.aplusoffice.cn/images/resource2/default_list.jpg"                    //  
        } else {
            res.typeBSearch[i].imgUrl = res.picUrl + res.typeBSearch[i].appPic                                              // '' 
        }
    }

    // ''( list-intro )  (  )
    for( let i=0; i<res.buildingRelationList.length; i++ ) {
        res.buildingRelationList[i].imgUrl = res.picUrl + res.buildingRelationList[i].appListUrl
    }

    // '' - Intro   
    for (var i = 0; i < res.buildingRelationList.length; i++) {
        res.buildingRelationList[i].badge = ''
    }

    //  ( ,DIV -  'buildingRelationList'length: 0, )
    let buildingRelationList_length = res.buildingRelationList.length
    // console.log('buildingRelationListlength' + buildingRelationList_length)                                      // (  -  )
    if ( buildingRelationList_length < 1 ) {
        console.log( 'buildingRelationList' )
    } else {
        state.coWorking.buildingRelationList_judgeShow  = false                                                             // 
        state.coWorking.buildingRelationList            = res.buildingRelationList                                          //  buildingRelationList 
    }
    // false
    setTimeout(function() {
        state.coWorking['judgeShow'] = false                                                                                // ,false
    },2000)
}

//  - 
export const addOfficeBuildingInfo =( state,res ) => {
    // state( ;  1., 2.buildingRelationList_judgeShow )
    state.officeBuilding = {
        judgeShow: true,
        buildingRelationList_judgeShow: true
    }
    // res   state ( judgeShow )
    state.officeBuilding.buildingDetails    = res.buildingDetails                                                           // (  +  )
    state.officeBuilding.buildingPicList    = res.buildingPicList                                                           // (  )
    state.officeBuilding.status12Search     = res.status12Search                                                            // List
    state.officeBuilding.status3Search      = res.status3Search                                                             // List

    // '' (  )
    for( let i=0; i<res.status12Search.length; i++ ) {
        res.status12Search[i].imgUrl = res.picUrl + res.status12Search[i].appPic                                      // '' 
    }
    // '' (  )
    for( let i=0; i<res.status3Search.length; i++ ) {
        res.status3Search[i].imgUrl = res.picUrl + res.status3Search[i].appPic                                        // '' 
    }

    // ''( list-intro )  (  )
    for( let i=0; i<res.buildingRelationList.length; i++ ) {
        res.buildingRelationList[i].imgUrl = res.picUrl + res.buildingRelationList[i].appListUrl
    }
    // '' - Intro   
    for (var i = 0; i < res.buildingRelationList.length; i++) {
        res.buildingRelationList[i].badge = ''
    }

    //  ( ,DIV -  'buildingRelationList'length: 0, )
    let buildingRelationList_length = res.buildingRelationList.length
    if ( buildingRelationList_length < 1 ) {
        console.log( 'buildingRelationList' )
    } else {
        state.officeBuilding.buildingRelationList_judgeShow  = false                                                        // 
        state.officeBuilding.buildingRelationList            = res.buildingRelationList                                     //  buildingRelationList 
    }
    let officeBuildingImgUrl_Arr = []                                                                                       // ()
    for(let i=0; i<res.buildingPicList.length;i++) {                                                                        //   - 
        function OfficeBuildingImgUrl(imgUrl) {
            this.imgUrl = res.picUrl + imgUrl
        }
        const officeBuildingImgUrl_Obj = new OfficeBuildingImgUrl( res.buildingPicList[i].realAppUrl )
        officeBuildingImgUrl_Arr.push( officeBuildingImgUrl_Obj )                                                           //  store
    }
    state.officeBuilding['imgUrl_Arr'] = officeBuildingImgUrl_Arr                                                           //  state.furnitureInfo 
    // false
    setTimeout(function() {
        state.officeBuilding['judgeShow'] = false                                                                           // ,false
    },2000)
}

