<!-- Service  ( : '' ) -->
<template>
    <div class="Service_dropDownMenu">
        <mu-dropDown-menu :value="kind" @change="kindChange">
            <mu-menu-item value="1" title=""/>
            <mu-menu-item value="2" title=""/>
            <mu-menu-item value="3" title=""/>
            <mu-menu-item value="4" title=""/>
            <mu-menu-item value="5" title=""/>
            <mu-menu-item value="6" title=""/>
        </mu-dropDown-menu>
        <mu-dropDown-menu :value="price" @change="priceChange">
            <mu-menu-item value="1" title=""/>
            <mu-menu-item value="2" title=""/>
        </mu-dropDown-menu>
    </div>
</template>

<script>
export default {
    data () {
        return {
            kind: '1',
            price: '1'
        }
    },
    methods: {
        kindChange (value) {
            this.kind = value
        },
        priceChange (value) {
            this.price = value
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../../../sass/main'

.Service_dropDownMenu
    position: fixed
    z-index: 2
    @extend %flexWrap
    width: 100%
    border-bottom: 1px solid rgba($badgeFont-color,.3)
    >.mu-dropDown-menu
        flex: 1
        +bC($F)
        &:first-child
            border-right: 1px solid rgba($badgeFont-color,.3)
</style>



