<!--  (  )  -->
<template>
    <div class="office-building_router">
        <!--  -->
        <CssLoading v-if="getOfficeBuildingInfo.judgeShow" />
        <!--   -->
        <div v-else>
            <DBAppBar :AppBar_title = getOfficeBuildingInfo.buildingDetails.name />
            <Swiper :swiper_arr = getOfficeBuildingInfo.imgUrl_Arr />
            <!--  -->
            <div class="intro">
                <h2> {{ getOfficeBuildingInfo.buildingDetails.name }} </h2>
                <ul>
                    <li>
                        <span> : </span>
                        <p class="origin"> {{ getOfficeBuildingInfo.buildingDetails.city }} </p>
                        <p> {{ getOfficeBuildingInfo.buildingDetails.reg }} </p>
                        <p class="end"> {{ getOfficeBuildingInfo.buildingDetails.road }} </p>
                    </li>
                    <li>
                        <span> : </span>
                        <p> <b> {{ getOfficeBuildingInfo.buildingDetails.priceDayMin }} ~ {{ getOfficeBuildingInfo.buildingDetails.priceDayMax }} /m/ </b> </p>
                    </li>
                    <li>
                        <span> : </span>
                        <p> {{ getOfficeBuildingInfo.buildingDetails.propertyCompany }} </p>
                    </li>
                    <li>
                        <span> : </span>
                        <p> {{ getOfficeBuildingInfo.buildingDetails.floorHeight }} </p>
                    </li>
                    <li>
                        <span> : </span>
                        <p> {{ getOfficeBuildingInfo.buildingDetails.parkingNum }} </p>
                    </li>
                </ul>
            </div>
            <!--  -->
            <div class="contentBox">
                <Title :title_info = titleInfo />
                <p> {{ getOfficeBuildingInfo.buildingDetails.description }} </p>
            </div>
            <!--  -->
            <div class="contentBox buildingDetails--list">
                <Title :title_info = status12Search />
                <ul>
                    <li v-for = "item in getOfficeBuildingInfo.status12Search">
                        <!--  -->
                        <div>
                            <img :src="item.imgUrl" class="listImg" >
                        </div>
                        <!--  -->
                        <p> {{ item.area }}m </p>
                        <!--   -->
                        <p class="priceMonth"> <b> {{ item.priceMonth }} </b> <spsn>/</spsn></p>
                        <!--    -->
                        <p class="priceDay"> <b> {{ item.priceDay }} </b><spsn>/m/</spsn></p>
                        <!--  -->
                        <!--<p> {{ item.statusStr }} </p>-->
                        <!--  -->
                        <p class="decorationStr"> {{ item.decorationStr }} </p>
                    </li>
                </ul>
            </div>
            <!--  -->
            <div class="contentBox buildingDetails--list">
                <Title :title_info = status3Search />
                <ul>
                    <li v-for = "item in getOfficeBuildingInfo.status3Search">
                        <!--  -->
                        <div>
                            <img :src="item.imgUrl" class="listImg" >
                        </div>
                        <!--  -->
                        <p> {{ item.area }}m </p>
                        <!--  -->
                        <p class="priceDay"> <b> {{ item.priceSale }} </b> <spsn>/</spsn> </p>
                        <!--  -->
                        <p> {{ item.floorStr }} </p>
                        <!-- (  ''  ) -->
                        <!--<p> {{ item.statusStr }} </p>-->
                        <!--  -->
                        <p> {{ item.decorationStr }} </p>
                    </li>
                </ul>
            </div>
            <!--  -->
            <div class="contentBox">
                <Title :title_info = reserve />
                <mu-icon slot="left" value="person"/> <mu-text-field label="" labelFloat/><br/>
                <mu-icon slot="left" value="phone"/> <mu-text-field label="" labelFloat/><br/>
                <mu-icon slot="left" value="vpn key"/>
                <mu-raised-button class="demo-raised-button" label="" icon="send" primary/>
            </div>
            <!--  -  -->
            <div class="contentBox">
                <Title :title_info = coWorking />
                <Intro :buildingIntro_Arr = getOfficeBuildingInfo.buildingRelationList />
            </div>
            <!--  -->
            <div class="contentBox">
                <Title :title_info = around />
                <Map :building_centrePoint = getOfficeBuildingInfo.buildingDetails />
            </div>
        </div>
    </div>
</template>

<script>
import  { mapActions, mapGetters }  from    'vuex'
import  DBAppBar                    from    './BuildingDetails_AppBar.vue'              // 
import  Intro                       from    './BuildingDetails_Intro.vue'               // Intro 
import  CssLoading                  from    '../Auto/CssLoading_1.vue'                  // 
import  Title                       from    '../Auto/Title.vue'                         // 
import  Swiper                      from    '../Discover/Swiper.vue'                    // 
import  DetailsList                 from    './BuildingDetails_List.vue'                //   +  
import  Map                         from    './BuildingDetails_Map.vue'                 //   (  )

const   components = { DBAppBar, Intro, CssLoading, Title, Swiper, DetailsList, Map }

export default {
    data() {
        return {
            titleInfo: {
                title: ''
                ,backgroundColor: '#FFF'
            }
            ,status12Search: {
                title: ''
                ,backgroundColor: '#FFF'
            }
            ,status3Search: {
                title: ''
                ,backgroundColor: '#FFF'
            }
            ,reserve: {
                title: ''
                ,backgroundColor: '#FFF'
            }
            ,coWorking: {
                title: ''
                ,backgroundColor: '#FFF'
            }
            ,around: {
                title: ''
                ,backgroundColor: '#FFF'
            }
        }
    }
    ,mounted: function() {
        this.setOfficeBuildingInfo()                                                // store 
        this.heightRevise()                                                         // List 
    }
    ,methods:{
        // : Action (  )
        setOfficeBuildingInfo () {
            this.$store.dispatch({
                type: 'setOfficeBuildingInfo',
                codeId: this.$route.params.id
            })
        }
        // : List . 1:1
        ,heightRevise: function(){
            // ,ajax,
            setTimeout(() => {
                let img_Arr     = document.getElementsByClassName('listImg')
                let img_width   = img_Arr[0].offsetWidth                            // 
                for (let i = 0; i < img_Arr.length; i++){
                    img_Arr[i].style.height = img_width + 'px'                      // 
                }
            }, 4500)
        }
    }
    ,computed: mapGetters({
        getOfficeBuildingInfo: 'getOfficeBuildingInfo'
    })
    ,components: components
}
</script>

<style lang="sass" scoped>
@import '../../sass/main'

.office-building_router
    //   -   ( Sass )
    +auto--BuildingDetails
</style>



