<!-- "" -->
<template>
    <div id="service">
        <mu-row gutter>
            <mu-col width="100" tablet="50" desktop="33" class="service-Box" v-for="item in service_info">
                <!--  -->
                <div v-if="item.imgLeftFloat" v-on:click="toUrl( item.hrefUrl )">
                    <img :src="item.imgUrl" />
                    <div class="service-Describe">
                        <div>
                            <h2> {{ item.title }} </h2>
                            <p> {{ item.subTitle }} </p>
                        </div>
                    </div>
                </div>

                <div v-else v-on:click="toUrl( item.hrefUrl )">
                    <div class="service-Describe">
                        <div>
                            <h2> {{ item.title }} </h2>
                            <p> {{ item.subTitle }} </p>
                        </div>
                    </div>
                    <img :src="item.imgUrl" />
                </div>
            </mu-col>
        </mu-row>
    </div>
</template>

<script>
export default {
    data() {
        return {
            service_info: [
                {
                    imgUrl: require('../assets/images/find.png'),
                    title: '',
                    subTitle: 'Find Properties',
                    imgLeftFloat: true,
                    hrefUrl: 'findProperties'
                },
                {
                    imgUrl: require('../assets/images/putIn.png'),
                    title: '',
                    subTitle: 'Put in Properties',
                    imgLeftFloat: false,
                    hrefUrl: 'putInProperties'
                },
                {
                    imgUrl: require('../assets/images/pic_furniture.png'),
                    title: '',
                    subTitle: 'Office Furniture',
                    imgLeftFloat: true,
                    hrefUrl: 'furniture'
                },
                {
                    imgUrl: require('../assets/images/pic_decoration.png'),
                    title: '',
                    subTitle: 'Office Decoration',
                    imgLeftFloat: false,
                    hrefUrl: 'decoration'
                },
                {
                    imgUrl: require('../assets/images/pic_research.png'),
                    title: '',
                    subTitle: 'Marketing Research',
                    imgLeftFloat: true,
                    hrefUrl: 'research'
                },
                {
                    imgUrl: require('../assets/images/pic_training.png'),
                    title: '',
                    subTitle: 'Staff Training ',
                    imgLeftFloat: false,
                    hrefUrl: 'training'
                },
                {
                    imgUrl: require('../assets/images/pic_computer.png'),
                    title: '',
                    subTitle: 'Office Computer',
                    imgLeftFloat: true,
                    hrefUrl: 'computer'
                }
            ]
        }
    },
    mounted: function () {
        this.heightRevise()
        this.setBottomBtnState(3)
    },
    methods: {
        // : service-Box. 1:1
        heightRevise: function(){
            // ,ajax,
            setTimeout(() => {
                const img_Arr = document.getElementsByTagName('img')
                const img_width = img_Arr[0].offsetWidth                //
                // console.log(img_width)
                for (var i = 0; i < img_Arr.length; i++){
                    img_Arr[i].style.height = img_width + 'px'
                }
            }, 100)
        },
        // : 
        toUrl: function (message) {
            location.href='#/service/' + message
        }
        // 
        ,setBottomBtnState(state) {
            let bottomBtn_Arr = document.getElementsByClassName("mu-buttom-item")
            // 
            for( let i = 0; i < bottomBtn_Arr.length; i++ ) {
                bottomBtn_Arr[i].setAttribute("class","mu-buttom-item router-link-active")
            }
            bottomBtn_Arr[state].setAttribute("class","mu-buttom-item router-link-active mu-bottom-item-active")        // ""
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../sass/main'

#service
    +REM(margin-bottom,80px)
    .service-Box >div
        @extend %flexWrap
        +bC($F)
        img,.service-Describe
            @extend %flexCenter
            width: 50%
            text-align: center
        h2
            +REM(font-size,$title-size)
            @extend %bold
            color: $EnterpriseService-color
            +REM(line-height,22px)
        p
            +REM(font-size,$badge-size)
            color: $badgeFont-color
</style>



