<!--  -->
<template>
    <div>
        <!-- AppBar -->
        <div id="search__appBar">
            <mu-text-field icon="search" class="appbar-search-field"  slot="right" hintText=""/>
            <mu-flat-button color="white" label="" slot="right" @click="goBack" />
        </div>
        <!--  -->
        <div class="search__hotSearch">

        </div>
    </div>
</template>

<script>
import VueRouter from 'vue-router'

export default {
    methods: {
        // : (  )
        goBack () {
            const router = new VueRouter()
            router.go(-1)
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../sass/main.sass'

#search__appBar
    +bC($F)
    // 
    .mu-text-field
        +REM(margin,$autoMargin/2)
        margin:
            left: 2%
            right: 2%
        width: 76% !important
        +bC($base-color)
    // 
    .mu-flat-button
        width: 19%
        +bC($F)
        color: $btnTitle-color !important
        // ""padding
        span.mu-flat-button-label
            padding: 0 !important
</style>



