<!--  -->
<template>
    <div id="newFeatures">
        <AutoTemplate :aboutAPlus_auto_Arr = newFeatures_Arr />
    </div>
</template>
<script>
import AutoTemplate         from    './AboutAPlus_autoTemplate.vue'         // 
const components = { AutoTemplate }
export default {
    data() {
        return {
            newFeatures_Arr: [
                {
                    title:         `1`,
                    textContent:   `1`
                },
                {
                    title:         `2`,
                    textContent:   `2`
                },
                {
                    title:         `3`,
                    textContent:   `3`
                }
            ]
        }
    },
    components: components
}
</script>
<style lang="sass">
@import '../../sass/main.sass'

#newFeatures
    +auto--contentBox($autoMargin,$title-color,$title-size,$badgeFont-color,$text-size)
    +bC($F)
</style>



